.primary-button {
    color: black;
    background-color: #9ecae1;
    font-family: 'Roboto', sans-serif;
    -webkit-box-shadow: 1px 1px #3182bd;
            box-shadow: 1px 1px #3182bd;
    font-size: 18px;   
    height: 2em;
    margin: 5px;
    border-radius: 7.5px;
    padding: 5px;
}

.primary-button:hover {
    -webkit-box-shadow: 0px 0px #3182bd;
            box-shadow: 0px 0px #3182bd;
    color: #deebf7;
}

.card-link {
    text-decoration: none;
    color: rgb(51, 51, 51);
}

.card-link:hover {
    text-decoration: none;
    color: black;
}

.masthead {
    font-family: 'Lobster', cursive;
    font-size: 32px;
    padding: 3px;
    margin-top: 0;
}

.spcl-warning {
    color: red;
    font-size: 16px;
    margin-left: 10px;
}

@media (max-width: 450px), (max-height: 350px) { 
    .masthead {
        font-size: 24px;    
    }
}


